import React, { Component } from 'react'
import { connect } from 'react-redux'
import { changeModo } from '../redux/actions';

const mapStateToProps = state => {
  return {
    modo: state.modo,
    modoTitle: state.modoTitle
  }
}

class IndexConnected extends Component {

  render () {
    return (
    <div>
      INDEX {this.props.modo}
      br
      
      <button>{this.props.modoTitle}</button>
    </div>
      )
  }
}



const Index = connect(mapStateToProps)(IndexConnected)

export default Index